/* Customize your main colors in :root variables */
:root {
  --main-background-color: #7f29bd;
  --card-background-color: #a09d9f;
  --card-background-lighter-color: #7A7F92;
  --main-text-color:rgb(255, 255, 255);
  --second-text-color:rgb(255, 255, 255);
  --title-text-color:rgb(101, 251, 14);
}

a{
color: #f60b0b;  text-decoration: none;
}

body{
  background-color:var(--main-background-color);
}

h2 {
  color: var(--main-text-color);
  font-size: 25px;
}
 p{
  color: var(--second-text-color);
  font-size: 20px;
}

h3{
font-weight: normal;
font-size: 40px;
}

.MuiFab-extended{
  height: auto;
  padding-right: 0 !important;
}

.MuiFab-label{
  font-weight: bold;
}

.MuiPaper-root{
  background-color: var(--card-background-color) !important;
}

.MuiButton-containedPrimary:not(.MuiButton-fullWidth){
  border-radius: 24px !important;
  padding: 6px 16px;
}

.MuiButton-label{
  font-size: 1.2em;
}

.MuiLinearProgress-colorPrimary{
  background-color:var(--main-text-color) !important;
}

.MuiLinearProgress-barColorPrimary{
  background-color:var(--title-text-color) !important;
}
